import React, { useState, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import MultiUpload from './MultiUpload';
import axios from 'axios'
import Input from './UI/Input/Input'
import { Editor } from '@tinymce/tinymce-react';
import { pathToAPI, tokenExpiredMsg, expNotifyDuration } from '../hooks/globals'

const ProdFormAdd = (props) => {
    const newProdStore = useSelector(state => state.newProd)
    const newPicsStore = useSelector(state => state.newPics)
    const dispath = useDispatch()
    const [isFilled1, setIsFilled1] = useState(false)
    const [isFilled2, setIsFilled2] = useState(false)

    const editorRef = useRef(null); //  tinyMCE

    function inputName(event) {
        dispath({ type: 'SetNewProdTitle', name: event.target.value})
        //  ,   
        if(event.target.value.length > 0) setIsFilled1(false);
        else setIsFilled1(true)
    }
    // //   :
    // function inputDescr(event) {
    //     dispath({ type: 'SetNewProdDescr', description: event.target.value})
    //     //  ,   
    //     if(event.target.value.length > 0) setIsFilled2(false);
    //     else setIsFilled2(true)
    // }
    // //  tinyMCE :
    // function inputDescr(editorText) {
    //     dispath({ type: 'SetNewProdDescr', description: editorText })
    //     //  ,   
    //     if(editorText.length > 0) setIsFilled2(false);
    //     else setIsFilled2(true)
    // }
    //  tinyMCE  ( 1):
    function inputDescrEditorChange(editorText) {
        dispath({ type: 'SetNewProdDescr', description: editorText })
    }
    //  tinyMCE  ( 2):
    function inputDescrEditorKeyUp(){
        //  ,   
        if(newProdStore.description.length > 0) setIsFilled2(false);
        else setIsFilled2(true)
    }
    //  tinyMCE onEditorChange    ,  onChange    input.    onKeyUp. , ,   ,  2 (inputDescrEditorChenge  inputDescrEditorKeyUp)
    
    let filteredValue
    function inputPrice(e) {
        //     
        filteredValue = e.target.value.replace(/[^.\d]+/g,"").replace( /^([^.]*\.)|\./g, '$1' )
        dispath({ type: 'SetNewProdPrice', price: filteredValue})
    }

    function checkForm(e) {
        e.preventDefault()
        let pointsToGo = 2
        //   
        if(newProdStore.name.length === 0) { setIsFilled1(true); pointsToGo-- }
        if(newProdStore.description.length === 0) { setIsFilled2(true); pointsToGo-- }
        //   -  
        (pointsToGo === 2) ? createProduct()
            : props.pushNotify("  ,<br>  ", "notifyRed")
    }
    // function createProduct() {
    async function createProduct() {
        console.log('>>> createProduct!')
        console.log('newProdStore: ', newProdStore);
        console.log('newProdStore.categoryId: ', newProdStore.categoryId);

        try {
            const response = await axios({
                method: 'post',
                url:  pathToAPI + '/products/create.php',
                data: JSON.stringify({
                    id: newProdStore.id,
                    name: newProdStore.name,
                    description: newProdStore.description,
                    pictures: newPicsStore,
                    price: Number(newProdStore.price).toFixed(2),
                    categoryId: newProdStore.categoryId,
                    userToken: localStorage.getItem('AuthToken'),
                }),
                // headers: {
                //     "Content-type": "application/json; charset=UTF-8"
                // }
            })
            console.log(response)
            //    create.php
            switch(response.data.message){
                case ' ':
                    setIsFilled1(false)
                    setIsFilled2(false)
                    props.create()
                    setIsFilled1(false)
                    setIsFilled2(false)
                    break
                case ' !':
                    props.pushNotify(tokenExpiredMsg, "notifyWelcome", expNotifyDuration)
                    // ########################################################
                    //   ,  ,      
                    // ########################################################
                    break
                case '  !  ':
                    props.pushNotify("  ! Alarm!  ", "notifyWelcome")
                    // ########################################################
                    //   ,  ,      
                    // ########################################################
                    break
                default:
                    console.log('   .\n  ( 200).     create.php,     ')
                    alert('    (.  )')
                    break
            } // switch

        } catch (e) {
            if(e === 'AxiosError: Request failed with status code 400') {
                alert(' (: 400)    (.  )')
                console.log('   .\n: 400 -  \n( create.php    ).')
            } else {
                alert('   .\n' + e)
                console.log(e)
            }
        }
    }// createProduct

    return (
        <div className={ isFilled2 ? 'alarm-on' : 'alarm-off' }>
            <h1>{ props.header }</h1>
            <p className='mb-10'>: <b>{ newProdStore.categoryName }</b></p>
            <form>
                :<br/>
                <Input
                    className={`form-input w540 ${(isFilled1) ? "notfilled" : ""}`}
                    value={ newProdStore.name }
                    onChange={ inputName }
                    placeholder=""
                />
                <div className="tooltip mb-10">
                { isFilled1 && <div className="tooltiptext ml-90 mt-7"> </div> }
                </div>
                
                <MultiUpload />
                :
                <Input
                    className={`form-input w100 text-center ml-10 mr-4 mb-10`}
                    value={ newProdStore.price }
                    onChange={ inputPrice }
                    placeholder=""
                /> .<br/>
                :<br/>
                <Editor
                apiKey="3y3tmb0410mxili346oflvurml2iv3mbgh02rkl6ix8f29g2"
                onInit={(evt, editor) => editorRef.current = editor}
                value={ newProdStore.description }
                onEditorChange={ inputDescrEditorChange }
                onKeyUp={ inputDescrEditorKeyUp }

                init={{
                    width: 540,
                    // max_width: 540,
                    height: 240,
                    menubar: false,
                    toolbar_mode: 'wrap',
                    image_advtab: true,
                    draggable_modal: true,//    image
                    // content_css: 'css/content.css',
                    // skin: 'light',
                    // skin_url: './tinymceskins/light/', //   
                    plugins: 'autolink fullscreen image link lists wordcount linkchecker code', //  quickbars -  
                    toolbar: 'bold italic underline | forecolor bullist | link image code fullscreen',
                }}
                />
                <div className="tooltip mb-10">
                { isFilled2 && <div className="tooltiptext ml-90 mt-m19"> </div> }
                </div>

                <button onClick={ checkForm } className='btn greenBtn'></button>
            </form><br/>
        </div>
    )
}

export default ProdFormAdd