import React, { useState } from 'react';
import axios from 'axios';
import Modal from './UI/Modal/Modal';
import { useSelector, useDispatch } from 'react-redux';
import Products from './Products';
import { pathToAPI, pathToGraphics, tokenExpiredMsg, expNotifyDuration } from '../hooks/globals'


const Categories = (props) => {
    // Redux
    const updCatId = useSelector(state => state.updCat.id)
    const dispath = useDispatch()

    const [idToDelete, setIdToDelete] = useState(0)
    const [modalCatName, setModalCatName] = useState('')
    const [delModal, setDelModal] = useState(false)

    function selectCategory(id, name, description) {
        //       -  action='updC'    (  getProducts)
        if(id === updCatId) {
            dispath({ type: 'SetActionUpdCAgain' })
            return
        }

        dispath({
            type: 'SetUpdCategory',
            id: id,
            name: name,
            description: description
        })

        props.getProducts(id)
    }

    function confirmDelete(id, name) {
        setIdToDelete(id)
        setModalCatName('   <br> <b>' + name + '</b>?')
        setDelModal(true)
    }

    //   
    function hideModal() {
        setDelModal(false)
    }

    // ########################################################
    //      
    //      .     
    // ########################################################

    //  .       
    async function deleteCategory() {

        try {
            const response = await axios({
                method: 'post',
                url:  pathToAPI + '/categories/delete.php',
                data: JSON.stringify({
                    id: idToDelete,
                    userToken: localStorage.getItem('AuthToken'),
                }),
                // headers: {
                //     "Content-type": "application/json; charset=UTF-8"
                // }
            })// axios
            console.log('response: ', response);

            //    delete.php
            switch(response.data.message){
                case ' ':
                    props.deleteCatsData( idToDelete )
                    setDelModal( false )
                    break
                case ' !':
                    props.pushNotify(tokenExpiredMsg, "notifyWelcome", expNotifyDuration)
                    // ########################################################
                    //   ,  ,      
                    // ########################################################
                    break
                case '  !  ':
                    // dispath({ type: 'SetAuthStatus', authStatus: 'denied' })
                    props.pushNotify("  ! Alarm!  ", "notifyWelcome")
                    // ########################################################
                    //   ,  ,      
                    // ########################################################
                    break
                default:
                    console.log('   .\n  ( 200).     delete.php,   ')
                    alert('    (.  )')
                    break
            } // switch

        } catch (e) {
            if(e === 'AxiosError: Request failed with status code 400') {
                alert(' (: 400)    (.  )')
                console.log('   \n: 400 -  ID\n(. update.php)')
            } else {
                alert('   \n' + e)
                console.log('   \n' + e)
            }
        }// try
    }

    return (
        <>
            <Modal
                visible={delModal}
                modalColors="modalOrange"
                header=""
                pic='alert'
                message={modalCatName}
                btn1Text=""
                btn2Text=""
                onfirmed={deleteCategory}
                hide={hideModal}
            />
            {/* <TransitionGroup className=""> */}
                {props.categories.map((cat) =>
                    // <CSSTransition
                    //     key={cat.id}
                    //     timeout={400}
                    //     classNames="prod"
                    // >
                    <div key={cat.id}>
                        <div
                            className={`catItem ${cat.id === updCatId ? "current" : ''}`}
                            onClick={() => selectCategory(cat.id, cat.name, cat.description)}
                        >
                            <img src={ pathToGraphics + '/folder_18px.png' } className='ml-4 mr-6' alt=''/>
                            <div className="grow1">
                                { cat.name } / { cat.description } [{ cat.id }]
                            </div>
                            <div
                                className='catBtn'
                                onClick={(e)=>props.selectAddProd(e, cat.id, cat.name)}
                                ><img src={ pathToGraphics + '/add2_18px.png' } alt=''/></div>
                            <div
                                className='catBtn'
                                onClick={(e) => {
                                    e.stopPropagation();
                                    confirmDelete(cat.id, cat.name)} }
                                ><img src={ pathToGraphics + '/delete_16px.png' } alt=''/></div>
                        </div>
                        <div>
                        {(cat.id === updCatId) && 
                            (!props.isProdsLoading ?
                            <Products
                                products={ props.products }
                                deleteProdsData={ props.deleteProdsData }
                                pushNotify={ props.pushNotify }
                            />
                            : <div className='ml-30'> ...</div> )
                        }
                        </div>
                    </div>
                    // </CSSTransition>
                )}
            {/* </TransitionGroup> */}
        </>
    )
}

export default Categories
// export default memo(Categories)