import React, { useEffect, useRef, useState, memo } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useDeletePics } from '../hooks/useDeletePics';
import axios from 'axios';
import { TransitionGroup, CSSTransition } from 'react-transition-group';
import { pathToAPI, uploadsFolder, prewFolder, pathToGraphics, maxNameLength } from '../hooks/globals'

const MultiUpload = () => {
    // Redux
    const dispath = useDispatch()
    const picsInDBStore = useSelector(state => state.picsInDB)
    const newPicsStore = useSelector(state => state.newPics)
    
    const { deleteNewPics } = useDeletePics()
    const refFiles = useRef()
    const [files, setFiles] = useState([])
    const [isInProgress, setIsInProgress] = useState(false)
    const [uploadPercentage, setUploadPercentage] = useState(0)
    const [uplBtnBlock, setUplBtnBlock] = useState(true)
    const [uplTitle, setUplTitle] = useState(false)

    //    newPics ( )
    useEffect(() => {
        if(newPicsStore.length === 0) {
            hideBarAndClean()
            setUplTitle(false)
        } else {
            setUplTitle(true)
        }
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [newPicsStore])
    
    //      
    const handleFilesUpload = () => {
        setFiles(refFiles.current.files)
        setIsInProgress(true)
        //   ,   ""  
        if(refFiles.current.files.length > 0) setUplBtnBlock(false)
        else setUplBtnBlock(true)
    }
    //  
    async function submitFiles(e){
        e.preventDefault() //   Firefox

        console.log('>>> submitFiles!')

        setUploadPercentage(0)
        setUplBtnBlock(true)

        let file
        let formData = new FormData();
        
        for( let i = 0; i < files.length; i++){
            file = files[i];
            // console.log('file: ', file);
            formData.append('files[' + i + ']', file);
        }// for

        try {
            const response = await axios({
                method: 'post',
                url: pathToAPI + '/products/multiupload.php',
                data: formData,
                // ########################################################
                // userToken: localStorage.getItem('AuthToken'),
                // ########################################################
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress: function(progressEvent) {
                    setUploadPercentage(Math.round( (progressEvent.loaded * 100) / progressEvent.total ))
                }
                // .bind(this)
            })
            console.log('response: ', response);
            
            if(!response.data.uplFiles) {
                alert('      (. )')
                console.log('!   response.data.uplFiles');
                return
            }

            //    php 
            if(response.data) {
                //   
                dispath({ type: 'SetFreeze', value: true})

                //   newPics    ,  
                for( let j = 0; j < response.data.uplFiles.length; j++) {
                    if (!newPicsStore.includes(response.data.uplFiles[j])) {
                        //      
                        dispath({ type: 'AddNewPics', newPics: response.data.uplFiles[j] })
                        // ,     tempArr.         
                    }
                }// for

            } else {
                alert(' (. )')
                console.log('     .\n  ( 200).     multiupload.php,   ')
            }
        } catch (e) {
            console.log(e)
        }// try
        
    }// submitFiles

    //    .        picsToDelete - ,    (   ,   ProdFormUpdate)
    function preparePicToDelete( e, filename ) {
        e.preventDefault()
        console.log('preparePicToDelete')
        console.log('filename: ', filename)

        if( !filename && typeof filename !== 'string' ) {
            console.log(': filename     string [. preparePicToDelete()]');
            return }
        //   
        dispath({ type: 'SetFreeze', value: true})
        //     filename
        const filteredArr = picsInDBStore.filter(item => item !== filename)
        // ""  ( ),      ProdFormUpdate
        dispath({ type: 'AddPicsToDelete', picsToDelete: filename })
        //     
        dispath({ type: 'UpdatePicsInDB', picsInDB: filteredArr })
    }// preparePicToDelete
    
// // ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
//     //    picsToDelete
//     function delPicsToDelete() {
//         if(picsToDeleteStore.length > 0) deleteFiles( picsToDeleteStore )
//         //  
//         dispath({ type: 'ClearPicsToDelete' })// this.picsToDelete = []
//     }
// // ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~

// // ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
//     //    picsToDelete   picsInDB
//     function getBackFiles() {
//         if(picsToDeleteStore.length > 0) 
//         dispath({ type: 'AddToPicsInDB', picsInDB: picsToDeleteStore })// this.$store.commit('addToPicsArr', this.picsToDelete)
//         //  
//         dispath({ type: 'ClearPicsToDelete'})// this.picsToDelete = []
//     }// getBackFiles
// // ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~

    //   ,  -    input
    function hideBarAndClean() {
        const delay = 350 //    transition  .fade   .list ( transition .7s   delay = 300 (350).   ~  transition)
        const afterSmooth = 600 //   .smooth transition: .5s (500 ),  afterSmooth   . , 600 ()
        // :   .fade   .list,     transition.       " "  .

        setTimeout(() => { setIsInProgress(false) }, delay);
        refFiles.current.value = null //   input (multiple)
        setTimeout(() => { setUploadPercentage(0) }, delay + afterSmooth);
    }

    return (
        <div className="graybg">
            <div className={(picsInDBStore.length > 0) ? 'picsArrBg pl-15 pt-15 pb-6 smooth smooth-show' : 'pl-15 smooth'}>
                <ul className="">
                    {picsInDBStore.map((item) => 
                        <li key={item}
                            className={item.includes('_COPY') ? 'flex-list orange' : 'flex-list'}>
                            <a href={uploadsFolder + item} className='' target='_blank' rel='noreferrer noopener' title=''
                            ><img src={prewFolder + item} className="prev" alt="" /></a>
                            <div className='picName'>
                                <a href={uploadsFolder + item} target='_blank' rel='noreferrer noopener' title='   '>{ item.length > maxNameLength ? item.substr(0, maxNameLength - 13) + ' ... ' + item.substr(-8, 8) : item }</a>
                            </div>
                            <div className='delPic'
                                onClick={(e)=>preparePicToDelete(e, item)}><img src={ pathToGraphics + '/delete2_20px.png' } alt=''/></div>
                        </li>
                    )}
                </ul>
            </div>

            <div className="multiSelect mb-15">
                <div className="flex mr-15">
                    <input type="file" id="files" ref={refFiles} accept=".png, .jpg, .jpeg, .gif" className="" multiple onChange={handleFilesUpload}/><br/>
                    <button
                        disabled={uplBtnBlock}
                        onClick={submitFiles}
                        ></button>
                </div>
                <div className={isInProgress ? 'smooth smooth-show' : 'smooth'}>
                    <div className="mt-20 mb-10">
                        { uploadPercentage } % <br/>
                        <div className="progress">
                        <div className="progress-bar" style={{ width: + uploadPercentage + '%' }}></div>
                        </div>
                    </div>
                </div>

                <span>
                    <CSSTransition
                            in={uplTitle}
                            timeout={500}
                            classNames="uplTitle"
                            mountOnEnter
                            unmountOnExit
                        >
                        <div className="mt-10 mb-10">  ({newPicsStore.length}):</div>
                    </CSSTransition>
                    <ul className="ml-15">
                    <TransitionGroup className="">
                        {newPicsStore.map(item =>
                        <CSSTransition
                            key={item}
                            timeout={500}
                            classNames="uplPics"
                            mountOnEnter
                            unmountOnExit
                        >
                            <li
                                className={item.includes('_COPY') ? 'flex-list orange' : 'flex-list'}
                                key={item}>                                
                                <img src={prewFolder + item} className="prev" alt="" />
                                <div className='picName'>
                                    { item.length > maxNameLength ? item.substr(0, maxNameLength - 13) + ' ... ' + item.substr(-8, 8) : item }
                                </div>
                                <div className='delPic'
                                    onClick={(e) => {
                                        e.preventDefault();
                                        deleteNewPics(item)} }
                                    ><img src={ pathToGraphics + '/delete2_20px.png' } alt=''/></div> 
                            </li>
                        </CSSTransition>
                        )}
                    </TransitionGroup>
                    </ul>
                </span>
            </div>
        </div>

    )
}

const memoMultiUpload = memo(MultiUpload, (prevProp, nextProp)=>{
    if(prevProp.newPicsStore === nextProp.newPicsStore) return true
    return false
})

export default memoMultiUpload