import axios from 'axios';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { pathToAPI } from './globals'

export function useToken() {
    // Redux
    const dispath = useDispatch()
    const [authMsg, setAuthMsg] = useState('')
    
    //   (   )
    async function createToken() {
        try {
            const response = await axios({
                method: 'post',
                url:  pathToAPI + '/access/',
                data: JSON.stringify({
                    command: 'create', //  create -  
                }),
            })// axios

            //    access/index.php
            if(response.data.status === "created" && response.data.authToken) {
                localStorage.setItem('AuthToken', response.data.authToken)
                dispath({ type: 'SetAuthStatus', authStatus: 'created' })
                return true
            } else if (response.data.status === "await" && response.data.timeLeftMsg) {
                dispath({ type: 'SetAuthStatus', authStatus: 'await' })
                setAuthMsg(response.data.timeLeftMsg)
                return
            } else {
                dispath({ type: 'SetAuthStatus', authStatus: 'await' })
                return
            }

        } catch (e) {
            if(e === 'AxiosError: Request failed with status code 400') {
                alert(' (: 400) (.  )')
                console.log('   .\n: 400\n(. access/index.php)')
                return
            } else {
                alert(':\n' + e)
                console.log(e)
                return
            }
        } // try
    } // createToken

    //    (     )
    async function checkPermissions(userToken) {
        if(!userToken) {
            console.log('\n  checkPermissions   userToken ( )\n ');
            userToken = ''
            // return
        }

        try {
            const response = await axios({
                method: 'post',
                url:  pathToAPI + '/access/',
                data: JSON.stringify({
                    command: 'check', //  check -  
                    userToken: userToken,
                }),
            })// axios

            //    access/index.php
            switch(response.data.status){
                case "approved":
                    console.log('status: approved')
                    dispath({ type: 'SetAuthStatus', authStatus: 'approved' })
                    return true
                    // break
                case "expired":
                    console.log('status: expired')
                    dispath({ type: 'SetAuthStatus', authStatus: 'expired' })
                    return
                    // break
                case "denied":
                    console.log('status: denied')
                    dispath({ type: 'SetAuthStatus', authStatus: 'denied' })
                    return
                    // break
                default:
                    dispath({ type: 'SetAuthStatus', authStatus: 'expired' })
                    return
                    // break
            }

        } catch (e) {
            if(e === 'AxiosError: Request failed with status code 400') {
                alert(' (: 400) (.  )')
                console.log('   \n: 400\n(. access/index.php)')
                return
            } else {
                alert(':\n' + e)
                console.log(e)
                return
            }
        } // try

    } // checkPermissions

    return { authMsg, createToken, checkPermissions }
}