import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios'
import './styles/App.css'
import Categories from './components/Categories';
import { useDispatch, useSelector } from 'react-redux';
import FormWelcome from './components/FormWelcome';
import FormDefault from './components/FormDefault';
import CatFormUpdate from './components/CatFormUpdate';
import ProdFormAdd from './components/ProdFormAdd';
import ProdFormUpdate from './components/ProdFormUpdate';
import Modal from './components/UI/Modal/Modal';
import { useDeletePics } from './hooks/useDeletePics';
import { useToken } from './hooks/useToken';
import CatFormAdd from './components/CatFormAdd';
import NotifyList from './components/NotifyList';
import { pathToAPI, tokenLifeTimeInfo, defNotifyDuration, welcNotifyDuration } from './hooks/globals'

function App() {
  const authStatus = useSelector(state => state.authStatus)
  const freeze = useSelector(state => state.freeze)
  const action = useSelector(state => state.action)
  const newCatStore = useSelector(state => state.newCat)
  const updCatStore = useSelector(state => state.updCat)
  const newProdStore = useSelector(state => state.newProd)
  const updProdStore = useSelector(state => state.updProd)
  const dispath = useDispatch()

  //   3 
  const { delPicsToDelete, deleteNewPics, getBackFiles } = useDeletePics()
  //     
  const { authMsg, createToken, checkPermissions } = useToken()
  const [isProdsLoading, setIsProdsLoading] = useState(false)
  const [isCatsLoading, setIsCatsLoading] = useState(false)
  const [categories, setCategories] = useState([])
  const [products, setProducts] = useState([])
  const [awayModal, setAwayModal] = useState(false)
  const [notifyList, setNotifyList] = useState([])

// ~~~~~~~~  ~~~~~~~~
// !   setTimeout  notifyRef.current,   notifyList.       notifyList     setTimeout,  notifyList      delNotify  .     useRef (. 2  )   notifyRef.current   
const notifyRef = useRef(notifyList);
notifyRef.current = notifyList;

//  
function delNotify(id) {
  setNotifyList( notifyRef.current.filter(item => item.id !== id) )
}

//  
function pushNotify(
  notifyText = "Default Notification Text", //   ( html)
  notifyClass = "notifyBlue", //  () 
  notifyDuration = defNotifyDuration //    ( )
  ){
  let id = Date.now() //   id
  // 1.    
  let newNotify = { id: id, notifyText: notifyText, class: notifyClass }
  setNotifyList([newNotify, ...notifyList])
  //     
  //     delNotify(id) - 
  // 2.    
  setTimeout(()=>{
      delNotify(id)
  }, notifyDuration)
}
// ~~~~~~~~ ~~~~~~~~~~ ~~~~~~~~

// ~~~~~~~~~~~~~~~~~~~~~~~~~
  //  ,     
  function awayConfirmed() {
    console.log('awayConfirmed!')
    setAwayModal(false)
    if(action === 'addP'){
      console.log('action (store): add');
      //  (  )    
      deleteNewPics()
    } else {
      console.log('action (store): upd');
      //  (  )    
      deleteNewPics()
      resetUpdForm()
    }
    
    //   
    dispath({ type: 'SetFreeze', value: false})
    pushNotify("  ")
  }
// ~~~~~~~~~~~~~~~~~~~~~~~~~

  //       
  function resetUpdForm() {
      //    picsToDelete   picsInDB
      getBackFiles()// this.$refs.updForm.$refs.uplModule.getBackFiles()

  }
  //   id ( id + 1)
  async function getNewCategoryId(){
    try {
      // setIsCatsLoading(true)
      const response = await axios.get( pathToAPI + '/categories/getNewId.php')

      if(response.data.newId) {
        return response.data.newId
      } else {
        console.log(' getNewId   id ( getNewId.php)\n', response);
        return
      }
    } catch (e) {
      console.log(e + '\n    id  ')
      return
    }
  }
  //   id ( id + 1)
  async function getNewId(){
    try {
      // setIsCatsLoading(true)
      const response = await axios.get( pathToAPI + '/products/getNewId.php')

      if(response.data.newId) {
        return response.data.newId
      } else {
        console.log(' getNewId   id ( getNewId.php)\n', response);
        return
      }
    } catch (e) {
      console.log(e + '\n    id  ')
      return
    }
  }
  //  " "
  async function selectAddCategory() {
    console.log(' ...')
    //  id   () 
    let newId = await getNewCategoryId()
    console.log('newId: ', newId);
    if(newId) dispath({ type: 'SetNewCat', id: newId })
  }
  //  " "
  async function selectAddProd(e, categoryId, categoryName) {
    e.stopPropagation()

    dispath({ type: 'SetUpdCatId', id: categoryId })
    getProducts(categoryId)

    console.log(' ...\n  (      - ,  ,            )')
    //    
    dispath({ type: 'UpdatePicsInDB', picsInDB: [] })
    //  id   () 
    let newId = await getNewId()
    if(newId) dispath({ type: 'SetNewProd', id: newId, categoryId: categoryId, categoryName: categoryName })

    // getProducts(categoryId)
  }
  //    ( )
  function createCat() {
    console.log('Func createCat() ');
    setCategories([...categories, newCatStore])
    //   name, description ( newCat),  id+1
    dispath({ type: 'ResetupNewCat' })
    //   
    dispath({ type: 'SetFreeze', value: false })
    pushNotify(" ", "notifyGreen")
  }
  //   ( )
  function updateCat() {
    //      ategories
    setCategories( categories.map(item => item.id === updCatStore.id ? {...updCatStore} : item) )
    //   
    dispath({ type: 'SetFreeze', value: false })
    pushNotify(" ", "notifyGreen")
  }
  //    ( )
  function createProd() {
    setProducts([...products, newProdStore])
    //   name, description ( newProd),  id+1
    dispath({ type: 'ResetupNewProd' })
    //   .  multiupload.jsx useEffect  hideBarAndClean ( -    input)
    dispath({ type: 'ClearNewPics' })
    //   
    dispath({ type: 'SetFreeze', value: false })
    // console.log('Prod Created!\n(    )')
    pushNotify(" ", "notifyGreen")
  }
  //   ( )
  function updateProd() {
    //      products
    setProducts( products.map(item => item.id === updProdStore.id ? {...updProdStore} : item) )
    //   .  multiupload.jsx useEffect  hideBarAndClean ( -    input)
    dispath({ type: 'ClearNewPics' })
    //   
    dispath({ type: 'SetFreeze', value: false })
    // console.log('Prod Updated!\n(    )')
    pushNotify(" ", "notifyGreen")
  }
  //  
  async function getCategories() {
    try {
      // isCatsLoading = true
      setIsCatsLoading(true)
      const response = await axios.get( pathToAPI + '/categories/')
      setCategories( response.data.records )
      console.log('categories: ', response.data.records);
    } catch (e) {
      alert('      \n' + e)
    } finally {
      setIsCatsLoading(false)
    }
  }

  //  
  async function getProducts(id) {
    setProducts([])
    try {
      setIsProdsLoading(true)
      const response = await axios({
        method: 'get',
        url:  pathToAPI + '/products/',
        params: {
          categoryId: id,
        },
      })// axios

      if(response.data.records){
        setProducts( response.data.records )        
      }
      
    } catch (e) {
      console.log(e + '\n      ')
    } finally {
      setIsProdsLoading(false)
    }
  }

  //   ( )
  function deleteCategoryData(id) {
    //   , ""  ,   action  default
    if(id === updCatStore.id) dispath({ type: 'SetActionDefault' })
    //   ( id)   products
    setCategories( categories.filter(value => value.id !== id) )
    // useEffect :     id
    pushNotify(" ", "notifyOrange")
  }

  //   ( )
  function deleteProductData(id) {
    console.log('deleteProductData  App! ');
    //   , ""  ,   action  default
    if(id === updProdStore.id) dispath({ type: 'SetActionDefault' })
    //   ( id)   products
    setProducts( products.filter(value => value.id !== id) )
    // useEffect :     id
    pushNotify(" ", "notifyOrange")
  }

  useEffect(() => {
    if(authStatus === 'created' || authStatus === 'approved') getCategories()
    if(authStatus === 'created') pushNotify(" !<br/>    <br/> " + tokenLifeTimeInfo, "notifyWelcome", welcNotifyDuration)
    
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [authStatus])

  useEffect(() => {
    let userToken = localStorage.getItem('AuthToken')
    //    ,   
    // (     )
    checkPermissions(userToken)
    
    //    :
    //  1 -    ,       10 . 
    //  2 -   , 

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])


  if(authStatus === 'appLoading'){
    return (
      <div className='flex-centered'>
        {/* <h1> ...</h1> */}
      </div>
    )
  }

  return (
    <>
      {(authStatus === 'created' || authStatus === 'approved') ?
        <div className="container">

        <NotifyList
          notifyList={ notifyList }
          delNotify={ delNotify }
          pushNotify={ pushNotify }
        />

        <div className="leftPart">

          {freeze && 
            <div className="freezeLayer"
              onClick={() => setAwayModal(true)}
            ></div> }

          <h1></h1>
          <button className="btn greenBtn-outline mb-10" onClick={selectAddCategory}> </button>

          {
          (!isCatsLoading ?
            <Categories
              isProdsLoading={ isProdsLoading }
              categories={ categories }
              products={ products }
              getProducts={ getProducts }
              selectAddProd={ selectAddProd }
              deleteCatsData={ deleteCategoryData }
              deleteProdsData={ deleteProductData }
              pushNotify={ pushNotify }
            />        
          : <div> ...</div> )}

        </div>
        <div className="rightPart">
          {(() => {
            switch (action) {
              case 'welcome':
                return <FormWelcome> </FormWelcome>
              case 'addC':
                return <CatFormAdd
                    header=" "
                    create={ createCat }
                    pushNotify={ pushNotify }
                />
              case 'updC':
                return <CatFormUpdate
                    header=" "
                    update={ updateCat }
                    pushNotify={ pushNotify }
                />
              case 'addP':
                return <ProdFormAdd
                    header=" "
                    create={ createProd }
                    pushNotify={ pushNotify }
                  />
              case 'updP':
                return <ProdFormUpdate
                    header=" "
                    update={ updateProd }
                    delPics={ delPicsToDelete }
                    pushNotify={ pushNotify }
                  />
              default:
                return <FormDefault>!</FormDefault>
            }
          })()}
        </div>
        <Modal
          visible={awayModal}
          modalColors="modalBlue"
          header="  "
          pic="info"
          message="   ?"
          btn1Text=""
          btn2Text=""
          onfirmed={awayConfirmed}
          hide={()=>setAwayModal(false)}
        />

      </div>
      :
      <div className='fill'>
        <div className='wrapper'>
        
        <div className='access-card w320'>
          <h1>React CMS</h1>
          {/* SPA</p> */}

          {authStatus === 'await' ?
            <div>C    .<br/>
              { authMsg }
            </div>
            : <></> }

          <button className="btn startBtn mt-15" onClick={ createToken }> </button><br/>

          <p className='mb-15'>
          {/*   React .<br/> */}
          {/*   API     */}

              ,       - <b></b>  <b> </b>.</p>
          <p className='mb-15'>
                <b>  { tokenLifeTimeInfo }</b>.</p>
          : web-design@ukr.net

        </div>
        </div>
      </div>
      }
    </>
  );
}

export default App;
